.skill-container {
	align-items: center;
	background: linear-gradient(135deg, transparent 3em, var(--soft-bg-color) 0);
	border-radius: 8px;
	/* box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px, rgba(17, 17, 26, 0.05) 0px 8px 32px; */
	color: var(--blocked-text-color);
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin: 1em;
	padding: 1em;
	position: relative;
}

.skill-language-icon {
	font-size: 3em;
}

.filled-star {
	color: #ebbd0c;
}